@import "./_common/base";
@import "./main";
@import "./layout";
@import "./etc";
@import "./intro_result";

/*
  z-index 가이드
  container : auto
  header :200
  header dimmed :100
*/

//기본 스타일
input[type=text],
textarea,
select {
    outline:none !important;
    @include transition(border-color, .2s);
    &:focus { border-color:$red !important;}
}

::selection { background:$red; color:$white; text-shadow:none; }

::-moz-selection { background:$red; color:$white; text-shadow:none; }

img { vertical-align:top; image-rendering:auto;}

.breadcrumb { display: none; }
 
.popup_dimmed {
    width:100%; height:100%; position:fixed; z-index:300; background-color:$black; display:none; top:0;
    @include opacity(0);
}
caption { width:1px; height:1px; overflow:hidden; margin:-1px;}

//퍼블 가이드용
.guide_line {
	div {
		&:after,
		&:before {width:1px; height:100%; display:block; content:""; position:fixed; background-color:$gray1; z-index:1000; opacity:0.3;}
		&.guide_70 {
			&:before {left:vw(40);}
			&:after {right:vw(40);}
		}
		&.guide_center {
			&:before {left:50%;}
		}
		&.guide_1180 {
			&:before {left:50%; margin:0 0 0 -590px;}
			&:after {left:50%; margin:0 0 0 590px;}
		}
		&.guide_1320 {
			&:before {left:50%; margin:0 0 0 -660px;}
			&:after {left:50%; margin:0 0 0 660px;}
		}
		&.guide_820 { 
			&:before {left:50%; margin:0 0 0 -410px;}
			&:after {left:50%; margin:0 0 0 410px;}
		}
	}
}

//히든 텍스트
.hidden-txt,
legend {overflow:hidden; display:inline-block; text-indent:-9999px; width:0; height:0; line-height:1px; position:absolute;}

header.navigation .for-mobile .menu .menu-wrap .top-link>li a img { vertical-align: inherit; }

.mo_only {
	display: block;
	@include desktop ()  { display: none; }
}
.pc_only {
	display: none;
	@include desktop ()  { display: block; }
}

